import React, { useContext, useMemo } from 'react';
import { AppContext } from '@components/common/context/AppProvider';
import { Card, CardContent, IconButton, Stack, Typography } from '@mui/material';
import BreedingUpgrades from '@components/account/Worlds/World4/Breeding/BreedingUpgrades';
import BreedingArena from '@components/account/Worlds/World4/Breeding/BreedingArena';
import Territory from '@components/account/Worlds/World4/Breeding/Territory';
import { handleCopyToClipboard, prefix, tryToParse } from 'utility/helpers';
import { NextSeo } from 'next-seo';
import Pets from '../../../components/account/Worlds/World4/Breeding/Pets';
import { getJewelBonus, getLabBonus } from '@parsers/lab';
import { getMealsBonusByEffectOrStat } from '@parsers/cooking';
import { getBubbleBonus } from '@parsers/alchemy';
import { getAchievementStatus } from '@parsers/achievements';
import { isMasteryBonusUnlocked } from '@parsers/misc';
import Timer from '../../../components/common/Timer';
import Tabber from '../../../components/common/Tabber';
import Tooltip from '@components/Tooltip';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import Button from '@mui/material/Button';
import { getVoteBonus } from '@parsers/world-2/voteBallot';
const Breeding = () => {
  const {
    state
  } = useContext(AppContext);
  const calcTimePerEgg = () => {
    const spelunkerObolMulti = getLabBonus(state?.account?.lab?.labBonuses, 8); // gem multi
    const blackDiamondRhinestone = getJewelBonus(state?.account?.lab?.jewels, 16, spelunkerObolMulti);
    const emeraldRhinestoneBonus = getJewelBonus(state?.account?.lab?.jewels, 11, spelunkerObolMulti);
    const mealBonus = getMealsBonusByEffectOrStat(state?.account, null, 'TimeEgg', blackDiamondRhinestone);
    const bubbleBonus = getBubbleBonus(state?.account?.alchemy?.bubbles, 'kazam', 'EGG_INK', false);
    const achievement = getAchievementStatus(state?.account?.achievements, 220);
    const skillMasteryBonus = isMasteryBonusUnlocked(state?.account?.rift, state?.account?.totalSkillsLevels?.breeding?.rank, 1);
    const voteBonus = getVoteBonus(state?.account, 16) || 1;
    return 7200 / (1 + (emeraldRhinestoneBonus + (mealBonus + (bubbleBonus + (10 * achievement + 15 * skillMasteryBonus * voteBonus)))) / 100) * 1000;
  };
  const handleCopy = async () => {
    const data = tryToParse(localStorage.getItem('rawJson'));
    const breedingData = tryToParse(data?.data?.Breeding);
    await handleCopyToClipboard(breedingData, false);
  };
  const timePerEgg = useMemo(() => calcTimePerEgg(), [state]);
  const now = new Date().getTime();
  return <>
      <NextSeo title="Breeding | Idleon Toolbox" description="Keep track of your breeding upgrades, eggs and arena upgrades" data-sentry-element="NextSeo" data-sentry-source-file="breeding.jsx" />
      <Stack direction={'row'} gap={2} alignItems={'center'} data-sentry-element="Stack" data-sentry-source-file="breeding.jsx">
        <Button onClick={handleCopy} data-sentry-element="Button" data-sentry-source-file="breeding.jsx"><ContentCopyIcon sx={{
          mr: 1
        }} data-sentry-element="ContentCopyIcon" data-sentry-source-file="breeding.jsx" />Copy breeding data</Button>
      </Stack>
      <Stack my={2} direction={'row'} alignItems={'center'} flexWrap={'wrap'} gap={2} data-sentry-element="Stack" data-sentry-source-file="breeding.jsx">
        {state?.account?.breeding?.eggs?.map((eggLevel, index) => {
        return eggLevel > 0 ? <Card key={`egg-${index}`}>
            <CardContent sx={{
            '&:last-child': {
              padding: '8px'
            },
            display: 'flex',
            alignItems: 'center'
          }}>
              <img src={`${prefix}data/PetEgg${eggLevel}.png`} alt="" />
            </CardContent>
          </Card> : null;
      })}
      </Stack>
      <Stack direction={'row'} gap={2} flexWrap={'wrap'} data-sentry-element="Stack" data-sentry-source-file="breeding.jsx">
        <Card data-sentry-element="Card" data-sentry-source-file="breeding.jsx">
          <CardContent data-sentry-element="CardContent" data-sentry-source-file="breeding.jsx">
            <Typography variant={'subtitle2'} data-sentry-element="Typography" data-sentry-source-file="breeding.jsx">Time to next egg</Typography>
            <Timer type={'countdown'} stopAtZero date={now + (timePerEgg - state?.account?.breeding?.timeToNextEgg)} lastUpdated={state?.lastUpdated} data-sentry-element="Timer" data-sentry-source-file="breeding.jsx" />
          </CardContent>
        </Card>
        <Card data-sentry-element="Card" data-sentry-source-file="breeding.jsx">
          <CardContent data-sentry-element="CardContent" data-sentry-source-file="breeding.jsx">
            <Typography variant={'subtitle2'} data-sentry-element="Typography" data-sentry-source-file="breeding.jsx">Time per egg</Typography>
            <Timer staticTime={true} date={new Date().getTime() + timePerEgg} data-sentry-element="Timer" data-sentry-source-file="breeding.jsx" />
          </CardContent>
        </Card>
      </Stack>
      <Typography variant={'caption'} data-sentry-element="Typography" data-sentry-source-file="breeding.jsx">*Time to next egg timer will be updated only when entering world 4
        town</Typography>
      <Tabber tabs={['Pets', 'Territory', 'Upgrades', 'Arena']} data-sentry-element="Tabber" data-sentry-source-file="breeding.jsx">
        <Pets {...state?.account?.breeding} lab={state?.account?.lab} account={state?.account} characters={state?.characters} lastUpdated={state?.lastUpdated} data-sentry-element="Pets" data-sentry-source-file="breeding.jsx" />
        <Territory {...state?.account?.breeding} spices={state?.account?.cooking?.spices} data-sentry-element="Territory" data-sentry-source-file="breeding.jsx" />
        <BreedingUpgrades petUpgrades={state?.account?.breeding?.petUpgrades} account={state?.account} meals={state?.account?.cooking?.meals} data-sentry-element="BreedingUpgrades" data-sentry-source-file="breeding.jsx" />
        <BreedingArena {...state?.account?.breeding} data-sentry-element="BreedingArena" data-sentry-source-file="breeding.jsx" />
      </Tabber>
    </>;
};
export default Breeding;